* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #1b1b1b;
  color: #ffffff;
}

:root {
  color-scheme: dark;
}

a,
button {
  all: unset;
  cursor: pointer;
  background: #ffffff;
  color: #000000;
  border: 2px solid #ff0000;
  padding: 10px;
}

.container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.links {
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: center;
  align-items: center;
  padding: 10px;
}

.video-container {
  width: 35%;
}

@media screen and (max-width: 1100px) {
  .video-container {
    width: 85%;
  }
}

.footer {
  padding: 10px;
}
